<template>
  <div class="full-card-style" @mouseenter="mouseover()" @mouseleave="mouseout()">
    <div class="full-card-img-container">
      <img v-lazy="cardVo.image">
      <transition name="full-card-action-fade">
          <div class="full-card-action-circle" v-show="flagCard"></div>
      </transition>
    </div>
    <div class="full-card-data-container">
      <div style="padding: 14px;background:rgba(18,18,19,.3);height:100%">
        <span>好吃的汉堡</span>
        <div>
          <time>{{ new Date() }}</time><br>
          introduction <br>
          The Modern Element However players can deactivate this at any time. <br>
          Players can even travel across planets if they wish to enter a different environment. <br>
          Phone Apps customers can access their ... read more <br>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    cardVo: Object
  },
  data () {
    return {
      flagCard: false
    }
  },
  mounted () {
    console.log('FullCard mounted loading')
  },
  methods: {
    mouseover () {
      this.flagCard = true
    },
    mouseout () {
      this.flagCard = false
    }
  }
}
</script>

<style scoped>
::selection{
  background-color: #01b18e;
  color: aliceblue;
}
.full-card-style{
  /* border: 1px solid red; */
  /* height: 420px; */
  color: aliceblue;
  height: 100%;
  min-height: 420px;
  overflow: hidden;
  position: relative;
  border-radius: 7px;
  transition: all .3s ease-in-out;
  box-shadow: 0px 2px 2px rgba(18,18,19,.5), 0px -1px 1px rgba(18,18,19,.2);
}
.full-card-style:hover{
  cursor: pointer;
  transform: translateY(-5px);
  box-shadow: 4px 4px 5px rgba(18,18,19,.3),-4px 0px 5px rgba(18,18,19,.3);
}
.full-card-img-container{
  position: relative;
  /* border: 1px dashed yellow; */
  /* height: 420px; */
  height: 100%;
  background: linear-gradient(to right bottom, #6d327c, #485DA6, #00a1ba, #01b18e, #32b37b);
}
img {
  width: 100%;
  /* border: 1px dashed green; */
  /* height: 420px; */
  height: 100%;
  object-fit: cover;
  transition: all 0.5s;
}
img:hover{
  transform: scale(1.1,1.1);
  opacity: 0.8;
}
.full-card-data-container{
  /* border: 1px dashed blue; */
  position: absolute;
  /* height: 100%; */
  width: 100%;
  /* z-index: 1; */
  bottom: 0;
}
.full-card-action-circle{
  position: absolute;
  z-index: 1;
  width: 70%;
  height: 80px;
  border-radius: 35px;
  left: 15%;
  top: 0;
  margin-top: -40px;
  background: rgba(18, 18, 19, .9);
  /* background: rgba(1, 177, 142, .5); */
  /* transition: all .7s; */
}
.full-card-action-fade-enter, .full-card-action-fade-leave-to{
  transform: translateY(-40px);
  /* opacity: 0.1; */
}
.full-card-action-fade-enter-active, .full-card-action-fade-leave-active{
  transition: all .3s ease 0s;
}
</style>



